      >
        <defs><clipPath id="a"><rect width="24" height="24" rx="0"></rect></clipPath><clipPath id="b"><rect width="24" height="24" rx="0"></rect></clipPath></defs><g clip-path="url(#a)"><path d="M7.952 20.471L21.753 4.658l-1.506-1.316L7.866 17.53l-4.152-4.23-1.428 1.402 5.666 5.77z" fill-rule="evenodd"></path></g>
      </svg>
    </span>
  </template>
  <script>
    export default {
      name: 'IconCheck',
      props: {
        size: {
          type: [Number, String],
          default: '1em'
        },
        color: {
          type: String,
          default: 'currentColor'
